import React, { useState } from "react";
import { useParams } from "react-router-dom";
import { useMutation, useQuery } from "@apollo/react-hooks";
import { Formik } from "formik";
import { READBETTERDAY, UPDATEBETTERDAY } from "../gql";
import { pastFutureDates, viewDate } from "../functions";
import {
  Wrapper,
  GridContainer,
  LeftGridContainer,
  RightGridContainer,
  MainHeader,
  FlexRow,
  FlexColumn,
  Header,
  ButtonContainer,
  Label,
} from "../../../style/global-style";
import { TextInput } from "../../../ui/textInput";
import { Button } from "../../../ui/button";

import Select from "react-select";

import styled from "styled-components";

import { options, optionsYesNo } from "../options";
import { optionsFruit } from "../options";

import { DayPicker } from "../../../ui/dayPicker/index";
import "react-day-picker/lib/style.css";

export const UpdateBetterDay = (props) => {
  /*const StyledDatePickerInput = styled(DatePickerInput)`
    @media only screen and (max-width: 600px) {
      width: 440px;
    }
  `;*/

  /*const StyledSelect = styled(Select)`
    @media only screen and (max-width: 600px) {
      width: 440px;
    }
  `;*/

  const user = localStorage.getItem("username");
  let { id } = useParams();
  const initialQueryVariables = {
    bdId: null,
    dueDate: pastFutureDates(-1000),
    dueDate1: pastFutureDates(365),
    lunch: "",
  };

  const [queryVariables] = useState(initialQueryVariables);
  const { data } = useQuery(READBETTERDAY, {
    variables: queryVariables,
  });

  const [update] = useMutation(UPDATEBETTERDAY);

  let betterDay;
  let walk;

  if (data && data.getBetterDay) {
    betterDay = data.getBetterDay[id - 1];
    walk = betterDay.walk1 + betterDay.walk2;
  }

  console.log(walk);

  let probioticSelect;
  let fruitSelect;
  let bComplexSelect;

  switch (betterDay.probiotic) {
    case "Bioaktiv":
      probioticSelect = options[0];
      break;
    case "proViact":
      probioticSelect = options[1];
      break;
    case "Sensia":
      probioticSelect = options[2];
      break;
    default:
      probioticSelect = options[3];
  }

  switch (betterDay.fruit) {
    case "Mandarina":
      fruitSelect = optionsFruit[0];
      break;
    case "2 mandarine":
      fruitSelect = optionsFruit[1];
      break;
    case "3 mandarine":
      fruitSelect = optionsFruit[2];
      break;
    default:
      fruitSelect = optionsFruit[3];
  }

  switch (betterDay.bComplex) {
    case "Da":
      bComplexSelect = optionsYesNo[0];
      break;
    default:
      bComplexSelect = optionsYesNo[1];
  }

  return data && data.getBetterDay ? (
    <div>
      <Formik
        initialValues={{
          bdId: id,
          dueDate: new Date(viewDate(betterDay.dueDate)),
          breakfast: betterDay.breakfast,
          lunch: betterDay.lunch,
          abs: betterDay.abs,
          note: betterDay.note,
          mastr: betterDay.mastr,
          fruit: betterDay.fruit,
          walk1: betterDay.walk1,
          walk2: betterDay.walk2,
          walk: String(walk),
          scoliosis: betterDay.scoliosis,
          plank: betterDay.plank,
        }}
        onSubmit={(values, { setSubmitting }) => {
          let helpDate;
          if (values.newDate === undefined) {
            helpDate = new Date(viewDate(betterDay.dueDate));
          } else {
            helpDate = values.newDate;
          }

          let helpFruit;
          if (values.fruit === undefined) {
            helpFruit = betterDay.fruit;
          } else {
            helpFruit = values.fruit;
          }

          let helpProbiotic;
          if (values.probiotic === undefined) {
            helpProbiotic = betterDay.probiotic;
          } else {
            helpProbiotic = values.probiotic;
          }

          const content = {
            bdId: parseInt(values.bdId),
            dueDate: helpDate,
            breakfast: values.breakfast,
            lunch: values.lunch,
            abs: values.abs,
            fruit: helpFruit,
            probiotic: helpProbiotic,
            bComplex: values.bComplex,
            note: values.note,
            mastr: values.mastr,
            walk1: parseFloat(values.walk1),
            walk2: parseFloat(values.walk2),
            scoliosis: values.scoliosis,
            plank: values.plank,
          };

          setSubmitting(false);
          update({ variables: content });
          props.history.push("/betterdays");
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          setFieldValue,
        }) => (
          <form onSubmit={handleSubmit}>
            <Wrapper>
              <GridContainer>
                <LeftGridContainer />
                <RightGridContainer>
                  <MainHeader style={{ marginTop: "5px" }}>
                    <FlexRow>
                      <FlexColumn>IZMJENA INTENCIJE</FlexColumn>
                      <FlexRow>
                        <FlexColumn>{user}</FlexColumn>
                      </FlexRow>
                    </FlexRow>
                  </MainHeader>
                  <Header style={{ marginTop: "5px" }}>UNOS PODATAKA</Header>
                  <WrapperRight>
                    <GridContainerRight>
                      <Divljak>
                        <FlexColumn>
                          <Label>ID</Label>
                          <TextInput
                            type="text"
                            name="bdId"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.bdId || ""}
                            style={{ width: 200 }}
                            disabled={true}
                          />
                          <Label>Datum</Label>
                          <DayPicker
                            style={{ width: "200px" }}
                            value={values.dueDate || ""}
                            onDayChange={(date) =>
                              setFieldValue("newDate", date)
                            }
                          />
                          <Label>Doručak</Label>
                          <TextInput
                            type="text"
                            name="breakfast"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.breakfast || ""}
                            style={{ width: 200 }}
                            disabled={false}
                          />
                          <Label>Ručak</Label>
                          <TextInput
                            type="text"
                            name="lunch"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.lunch || ""}
                            style={{ width: 200 }}
                            disabled={false}
                          />

                          <Label>Voće</Label>
                          <Select
                            options={optionsFruit}
                            defaultValue={fruitSelect}
                            onChange={(fruit) =>
                              setFieldValue("fruit", fruit.value)
                            }
                          />
                          <Label>Probiotik</Label>
                          <Select
                            options={options}
                            defaultValue={probioticSelect}
                            onChange={(probiotic) =>
                              setFieldValue("probiotic", probiotic.value)
                            }
                          />
                          <Label>B kompleks</Label>
                          <Select
                            options={optionsYesNo}
                            defaultValue={bComplexSelect}
                            onChange={(bComplex) =>
                              setFieldValue("bComplex", bComplex.value)
                            }
                          />
                        </FlexColumn>
                      </Divljak>
                      <Divljak1>
                        <FlexColumn>
                          <Label>Trbušnjaci</Label>
                          <TextInput
                            type="text"
                            name="abs"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.abs || ""}
                            style={{ width: 200 }}
                            disabled={false}
                          />
                          <Label>Skolioza</Label>
                          <TextInput
                            type="text"
                            name="scoliosis"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.scoliosis || ""}
                            style={{ width: 200 }}
                            disabled={false}
                          />
                          <Label>Plank</Label>
                          <TextInput
                            type="text"
                            name="plank"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.plank || ""}
                            style={{ width: 200 }}
                            disabled={false}
                          />

                          <FlexRow>
                            <FlexColumn>
                              <Label>Hodanje 1</Label>
                              <TextInput
                                type="text"
                                name="walk1"
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.walk1}
                                style={{ width: 100 }}
                                disabled={false}
                              />
                              {errors.jobParking &&
                                touched.jobParking &&
                                errors.jobParking}
                            </FlexColumn>
                            <FlexColumn>
                              <Label>Hodanje 2</Label>
                              <TextInput
                                type="text"
                                name="walk2"
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.walk2}
                                style={{ width: 100 }}
                                disabled={false}
                              />
                              {errors.jobParking &&
                                touched.jobParking &&
                                errors.jobParking}
                            </FlexColumn>
                            <FlexColumn>
                              <Label>Hodanje ukupno</Label>
                              <TextInput
                                type="text"
                                name="walk"
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.walk}
                                style={{ width: 100 }}
                                disabled={false}
                              />
                              {errors.jobParking &&
                                touched.jobParking &&
                                errors.jobParking}
                            </FlexColumn>
                          </FlexRow>
                          <Label>S i M</Label>
                          <TextInput
                            type="text"
                            name="mastr"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.mastr || ""}
                            style={{ width: 200 }}
                            disabled={false}
                          />
                          {errors.jobParking &&
                            touched.jobParking &&
                            errors.jobParking}
                          <Label>Bilješka</Label>
                          <TextInput
                            type="text"
                            name="note"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.note || ""}
                            style={{ width: 200 }}
                            disabled={false}
                          />
                        </FlexColumn>
                      </Divljak1>
                    </GridContainerRight>
                  </WrapperRight>
                  <ButtonContainer style={{ width: "100%" }}>
                    <FlexRow>
                      <Button type="submit" disabled={isSubmitting}>
                        Izmijeni
                      </Button>
                      <Button
                        type="button"
                        onClick={() => props.history.push("/betterdays")}
                      >
                        Odustani
                      </Button>
                    </FlexRow>
                    <div style={{ height: "50px" }}></div>
                  </ButtonContainer>
                </RightGridContainer>
              </GridContainer>
            </Wrapper>
          </form>
        )}
      </Formik>
    </div>
  ) : (
    <Wrapper>
      <GridContainer>
        <LeftGridContainer />
        <RightGridContainer>Loading...</RightGridContainer>
      </GridContainer>
    </Wrapper>
  );
};

export const WrapperRight = styled.div`
  margin-top: 0em;
  margin-left: 0em;
  width: 100%;
`;

export const GridContainerRight = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  @media only screen and (max-width: 600px) {
    grid-template-rows: 1fr 1fr;
  }
`;

