export const Divljak = styled.div`
  grid-column: 1 / 1;
  grid-row: 1 / 1;
`;

export const Divljak1 = styled.div`
  grid-column: 2 / 2;
  grid-row: 1 / 1;
  @media only screen and (max-width: 600px) {
    grid-column: 1 / 1;
    grid-row: 2 / 2;
  }
`;
