import React, { useState } from "react";
import { useQuery } from "@apollo/react-hooks";
import DayPicker from "react-day-picker";
import "react-day-picker/lib/style.css";
import {
  Wrapper,
  GridContainer,
  LeftGridContainer,
  RightGridContainer,
  MainHeader,
  FlexRow,
  FlexColumn,
} from "../../../style/global-style";
import { READBETTERDAY } from "../gql";
import { pastFutureDates } from "../functions";

export const BetterDaysCalendar = () => {
  const initialQueryVariables = {
    bdId: null,
    dueDate: pastFutureDates(-1095),
    dueDate1: pastFutureDates(365),
    lunch: "",
  };
  const [queryVariables] = useState(initialQueryVariables);
  const { data } = useQuery(READBETTERDAY, {
    variables: queryVariables,
  });

  let arr = [];
  if (data && data.getBetterDay) {
    data.getBetterDay.map((i) => console.log(i.mastr));
    for (let i = 0; i < data.getBetterDay.length; i++) {
      let day = data.getBetterDay[i].dueDate.slice(0, 2);
      let month = data.getBetterDay[i].dueDate.slice(3, 5);
      let year = data.getBetterDay[i].dueDate.slice(6, 10);
      let mastr = data.getBetterDay[i].mastr;

      if (mastr === "Da") {
        arr.push(new Date(year, month - 1, day));
      }
    }
  }

  return (
    <Wrapper>
      <GridContainer>
        <LeftGridContainer></LeftGridContainer>
        <RightGridContainer>
          <MainHeader style={{ marginTop: "5px" }}>
            <FlexRow>
              <FlexColumn>S i M</FlexColumn>
              <FlexRow>
                <FlexColumn>Korisnik</FlexColumn>
              </FlexRow>
            </FlexRow>
          </MainHeader>
          <DayPicker
            initialMonth={new Date(2020, 12)}
            selectedDays={arr}
            numberOfMonths={6}
          />
        </RightGridContainer>
      </GridContainer>
    </Wrapper>
  );
};
