import React, { useState, useEffect } from "react";
import { useMutation, useQuery } from "@apollo/react-hooks";
import { Formik } from "formik";
import Select from "react-select";
import {
  Wrapper,
  GridContainer,
  LeftGridContainer,
  RightGridContainer,
  MainHeader,
  FlexRow,
  FlexColumn,
  Header,
  ButtonContainer,
  Label,
} from "../../../style/global-style";
import { Button } from "../../../ui/button";

import { TextInput } from "../../../ui/textInput";
import { CREATEBETTERDAY, READBETTERDAY } from "../gql";

import { pastFutureDates } from "../functions";

import { options, optionsFruit, optionsYesNo } from "../options";

import styled from "styled-components";

import { DayPicker } from "../../../ui/dayPicker/index";

import "react-day-picker/lib/style.css";

export const CreateBetterDay = (props) => {
  /*const StyledDatePickerInput = styled(DatePickerInput)`
    @media only screen and (max-width: 600px) {
      width: 440px;
    }
  `;

  const StyledSelect = styled(Select)`
    @media only screen and (max-width: 600px) {
      width: 440px;
    }
  `;*/

  const user = localStorage.getItem("username");

  const [id, setId] = useState();

  const initialQueryVariables = {
    bdId: null,
    dueDate: pastFutureDates(-1095),
    dueDate1: pastFutureDates(365),
    lunch: "",
  };
  const [queryVariables] = useState(initialQueryVariables);
  const { data, refetch } = useQuery(READBETTERDAY, {
    variables: queryVariables,
  });

  const [add] = useMutation(CREATEBETTERDAY);

  useEffect(() => {
    if (data && data.getBetterDay) {
      setId(data.getBetterDay.length + 1);
    }
  }, [data]);

  useEffect(() => {
    refetch(READBETTERDAY);
  });

  return (
    <div>
      <Formik
        initialValues={{
          bdId: id,
          breakfast: "",
          lunch: "",
          dueDate: new Date(),
          paid: false,
          fruit: "",
          probiotic: "",
          bComplex: "",
          abs: "0",
          note: "",
          mastr: "",
          walk1: "",
          walk2: "",
          scoliosis: "",
          plank: "",
        }}
        onSubmit={(values, { setSubmitting }) => {
          const content = {
            bdId: id,
            breakfast: values.breakfast,
            lunch: values.lunch,
            fruit: values.fruit,
            probiotic: values.probiotic,
            bComplex: values.bComplex,
            abs: values.abs,
            dueDate: values.date !== undefined ? values.date : new Date(),
            note: values.note,
            mastr: values.mastr,
            walk1: parseFloat(values.walk1),
            walk2: parseFloat(values.walk2),
            scoliosis: values.scoliosis,
            plank: values.plank,
          };

          setSubmitting(false);

          add({ variables: content });
          props.history.push("/betterdays");
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          setFieldValue,
        }) => (
          <form onSubmit={handleSubmit}>
            <Wrapper>
              <GridContainer>
                <LeftGridContainer />
                <RightGridContainer>
                  <MainHeader style={{ marginTop: "5px" }}>
                    <FlexRow>
                      <FlexColumn>NOVI BOLJI DAN</FlexColumn>
                      <FlexRow>
                        <FlexColumn>{user}</FlexColumn>
                      </FlexRow>
                    </FlexRow>
                  </MainHeader>
                  <Header style={{ marginTop: "5px" }}>UNOS PODATAKA</Header>
                  <WrapperRight>
                    <GridContainerRight>
                      <Divljak>
                        <FlexColumn>
                          <Label>ID</Label>
                          <TextInput
                            width="600px"
                            type="text"
                            name="bdId"
                            onChange={handleChange}
                            //onBlur={handleBlur}
                            value={id || ""}
                            style={{ width: 200 }}
                            disabled={true}
                          />
                          {errors.jobParking &&
                            touched.jobParking &&
                            errors.jobParking}
                          <Label>Datum</Label>
                          {/*<DatePickerInput
                            value={values.dueDate}
                            className="my-custom-datepicker-component"
                            autoClose={true}
                            onChange={(date) => setFieldValue("date", date)}
                          />*/}
                          <DayPicker
                            style={{ width: "200px" }}
                            value={values.dueDate || ""}
                            //onDayChange={(day) => console.log(day)}
                            onDayChange={(date) =>
                              setFieldValue("newDate", date)
                            }
                            //onChange={(date) => setFieldValue("newDate", date)}
                          />
                          <Label>Doručak</Label>
                          <TextInput
                            type="text"
                            name="breakfast"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.breakfast}
                            style={{ width: 200 }}
                            disabled={false}
                          />
                          {errors.jobParking &&
                            touched.jobParking &&
                            errors.jobParking}
                          <Label>Ručak</Label>
                          <TextInput
                            type="text"
                            name="lunch"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.lunch}
                            style={{ width: 200 }}
                            disabled={false}
                          />
                          {errors.jobParking &&
                            touched.jobParking &&
                            errors.jobParking}
                          <Label>Voće</Label>
                          <Select
                            options={optionsFruit}
                            onChange={(fruit) =>
                              setFieldValue("fruit", fruit.value)
                            }
                          />
                          <Label>Probiotik</Label>
                          <Select
                            options={options}
                            onChange={(probiotic) =>
                              setFieldValue("probiotic", probiotic.value)
                            }
                          />
                          <Label>B kompleks</Label>
                          <Select
                            options={optionsYesNo}
                            onChange={(bComplex) =>
                              setFieldValue("bComplex", bComplex.value)
                            }
                          />
                        </FlexColumn>
                      </Divljak>
                      <Divljak1>
                        {" "}
                        <FlexColumn style={{ marginLeft: "0px" }}>
                          <Label>Trbušnjaci</Label>
                          <TextInput
                            type="text"
                            name="abs"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.abs}
                            style={{ width: 200 }}
                            disabled={false}
                          />
                          {errors.jobParking &&
                            touched.jobParking &&
                            errors.jobParking}
                          <Label>Skolioza</Label>
                          <TextInput
                            type="text"
                            name="scoliosis"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.scoliosis}
                            style={{ width: 200 }}
                            disabled={false}
                          />
                          {errors.jobParking &&
                            touched.jobParking &&
                            errors.jobParking}
                          <Label>Plank</Label>
                          <TextInput
                            type="text"
                            name="plank"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.plank}
                            style={{ width: 200 }}
                            disabled={false}
                          />
                          {errors.jobParking &&
                            touched.jobParking &&
                            errors.jobParking}
                          <FlexRow>
                            <FlexColumn>
                              <Label>Hodanje 1</Label>
                              <TextInput
                                type="text"
                                name="walk1"
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.walk1}
                                style={{ width: 200 }}
                                disabled={false}
                              />
                              {errors.jobParking &&
                                touched.jobParking &&
                                errors.jobParking}
                            </FlexColumn>
                            <FlexColumn>
                              <Label>Hodanje 2</Label>
                              <TextInput
                                type="text"
                                name="walk2"
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.walk2}
                                style={{ width: 200 }}
                                disabled={false}
                              />
                              {errors.jobParking &&
                                touched.jobParking &&
                                errors.jobParking}
                            </FlexColumn>
                          </FlexRow>
                          <Label>S i M</Label>
                          <TextInput
                            type="text"
                            name="mastr"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.mastr}
                            style={{ width: 200 }}
                            disabled={false}
                          />
                          {errors.jobParking &&
                            touched.jobParking &&
                            errors.jobParking}
                          <Label>Bilješka</Label>
                          <TextInput
                            type="text"
                            name="note"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.note}
                            style={{ width: 200 }}
                            disabled={false}
                          />
                          {errors.jobParking &&
                            touched.jobParking &&
                            errors.jobParking}
                        </FlexColumn>
                      </Divljak1>
                    </GridContainerRight>
                  </WrapperRight>
                  <ButtonContainer style={{ width: "100%", marginTop: "0px" }}>
                    <FlexRow>
                      <Button type="submit" disabled={isSubmitting}>
                        Dodaj
                      </Button>
                      <Button
                        type="button"
                        onClick={() => props.history.push("/betterdays")}
                      >
                        Odustani
                      </Button>
                    </FlexRow>
                    <div style={{ height: "50px" }}></div>
                  </ButtonContainer>
                </RightGridContainer>
              </GridContainer>
            </Wrapper>
          </form>
        )}
      </Formik>
    </div>
  );
};

export const WrapperRight = styled.div`
  margin-top: 0em;
  margin-left: 0em;
  width: 100%;
`;

export const GridContainerRight = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  @media only screen and (max-width: 600px) {
    grid-template-rows: 1fr 1fr;
  }
`;

/*export const Divljak = styled.div`
  border: 0px solid;
  width: 100%;

 
 
`;*/

export const Divljak = styled.div`
  grid-column: 1 / 1;
  grid-row: 1 / 1;
`;

export const Divljak1 = styled.div`
  grid-column: 2 / 2;
  grid-row: 1 / 1;
  @media only screen and (max-width: 600px) {
    grid-column: 1 / 1;
    grid-row: 2 / 2;
  }
`;
