import React, { useState, useEffect } from "react";
import {
  StyledPage,
  StyledTitleCard,
  StyledHeaderContainer,
  StyledTitleText,
  StyledGridContainer,
  StyledGridContainerTop,
} from "../style/workorderpdf";
import { useQuery } from "@apollo/react-hooks";
import { useParams } from "react-router-dom";
import { READBETTERDAY } from "../gql";
import { colorPalette } from "../../../style/theme";
import { pastFutureDates } from "../functions";

export const ReadBetterDay = () => {
  let { id } = useParams();

  const initialQueryVariables = {
    bdId: null,
    dueDate: pastFutureDates(-1095),
    dueDate1: "2020-12-31",
    lunch: "",
  };

  const [queryVariables] = useState(initialQueryVariables);

  const { data, refetch } = useQuery(READBETTERDAY, {
    variables: queryVariables,
  });

  let betterDay;
  let lunch;

  let helpPaid;

  if (data && data.getBetterDay) {
    betterDay = data.getBetterDay[id - 1];
    lunch = betterDay.lunch;
  }

  //paid === true ? (helpPaid = "Da") : (helpPaid = "Ne");

  useEffect(() => {
    refetch(READBETTERDAY);
  });

  return data && data.getBetterDay ? (
    <div>
      <PageContent>
        <StyledHeaderContainer>
          <StyledTitleText
            style={{
              backgroundColor: colorPalette.accent100,
              color: colorPalette.primary100,
              width: "100%",
            }}
          >
            INTENCIJA REDNI BROJ - {lunch}
          </StyledTitleText>
        </StyledHeaderContainer>
        <StyledTitleCard style={{ backgroundColor: colorPalette.primary100 }}>
          Spiza
        </StyledTitleCard>
        <StyledGridContainerTop>
          <div>Doručak:</div>
          <div>{betterDay.breakfast}</div>
          <div>Ručak:</div>
          <div>{betterDay.lunch}</div>
        </StyledGridContainerTop>
        <StyledGridContainerTop>
          <div>Intencija:</div>
          <div>{betterDay.lunch}</div>
        </StyledGridContainerTop>
        <StyledTitleCard style={{ backgroundColor: colorPalette.primary100 }}>
          Vježbanje
        </StyledTitleCard>
        <StyledGridContainer>
          <div>Datum:</div>
          <div>{betterDay.dueDate}</div>
          <div>Redni broj:</div>
          <div>{id}</div>
          <div style={{ marginTop: "10px" }}>Plaćeno:</div>
          <div style={{ marginTop: "10px" }}>{helpPaid}</div>
          <div style={{ marginTop: "10px" }}>Bilješka:</div>
          <div style={{ marginTop: "10px" }}>{betterDay.note}</div>
        </StyledGridContainer>
      </PageContent>
    </div>
  ) : null;
};

const PageContent = (props) => {
  return (
    <StyledPage>
      <div
        style={{
          overflow: "hidden",
          display: "flex",
          flexDirection: "column",
          flex: 1,
          padding: "0.5cm",
          margin: "0.5cm",
          border: "1px solid black",
        }}
      >
        {props.children}
      </div>
    </StyledPage>
  );
};
