import gql from "graphql-tag";

export const CREATEBETTERDAY = gql`
  mutation(
    $bdId: Int
    $dueDate: Date
    $breakfast: String
    $lunch: String
    $fruit: String
    $probiotic: String
    $bComplex: String
    $abs: String
    $note: String
    $mastr: String
    $walk1: Float
    $walk2: Float
    $scoliosis: String
    $plank: String
  ) {
    addBetterDay(
      bdId: $bdId
      dueDate: $dueDate
      breakfast: $breakfast
      lunch: $lunch
      fruit: $fruit
      probiotic: $probiotic
      bComplex: $bComplex
      abs: $abs
      note: $note
      mastr: $mastr
      walk1: $walk1
      walk2: $walk2
      scoliosis: $scoliosis
      plank: $plank
    ) {
      bdId
      dueDate
      breakfast
      lunch
      fruit
      probiotic
      bComplex
      abs
      note
      mastr
      walk1
      walk2
      scoliosis
      plank
    }
  }
`;

export const READBETTERDAYS = gql`
  query {
    getBetterDays {
      bdId
      dueDate
      fruit
      lunch
      probiotic
      mastr
      abs
      walk1
      walk2
      scoliosis
      plank
    }
  }
`;

