export const READBETTERDAY = gql`
  query($bdId: Int, $lunch: String, $dueDate: Date, $dueDate1: Date) {
    getBetterDay(
      bdId: $bdId
      lunch: $lunch
      dueDate: $dueDate
      dueDate1: $dueDate1
    ) {
      bdId
      dueDate
      lunch
      abs
      breakfast
      probiotic
      bComplex
      fruit
      note
      mastr
      walk1
      walk2
      scoliosis
      plank
    }
  }
`;

export const DELETEBETTERDAY = gql`
  mutation($bdId: Int) {
    deleteBetterDay(bdId: $bdId) {
      bdId
    }
  }
`;
