import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import { PrivateRoute } from "./PrivateRoute";
import { AdminPrivateRoute } from "./AdminPrivateRoute";
import { Login } from "../components/login/Login";
import {
  ReadBetterDay,
  BetterDays,
  CreateBetterDay,
  UpdateBetterDay,
  BetterDaysCalendar,
} from "../components/betterdays";
import { Users, UserNew, UserChange } from "../components/admin";

export const Routes = () => {
  return (
    <Switch>
      <Route exact path="/" render={() => <Redirect to="/betterdays" />} />
      <Route path="/login" component={Login} />
      {/* ******************************************** */}
      <PrivateRoute path="/betterdays" component={BetterDays} />
      <PrivateRoute path="/calendar" component={BetterDaysCalendar} />
      <PrivateRoute path="/newbetterday" component={CreateBetterDay} />
      <PrivateRoute path="/updatebetterday/:id" component={UpdateBetterDay} />
      <PrivateRoute path="/pdfbetterday/:id" component={ReadBetterDay} />
      {/* ******************************************** */}
      <AdminPrivateRoute path="/newuser" component={UserNew} />
      <PrivateRoute path="/changeuser/:id" component={UserChange} />)
      <AdminPrivateRoute path="/users" component={Users} />
    </Switch>
  );
};
